@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;700;900&display=swap);
*{
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*{
  font-family: 'Montserrat', sans-serif;
}
body{
  background-color: #462446;
}

.main-page{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #462446;
  color: #fff;
  height: 100%;
}

.menu__settings-logo{
  position: absolute;
  top: 20px;
  left: 15px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.menu__settings-logo img{
  width: 100%;
  height: 100%;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
.menu{
  -webkit-align-self: flex-start;
          align-self: flex-start;
  justify-self: flex-start;
}

.game-wrapper{
  height: 100vh;
  width: 100%;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.start-game__btn{
  background-color: #462446;
  color: #fff;
  border: 1px solid #fff;
  padding: 15px 20px;
  margin-bottom: 25px;
  cursor: pointer;
  transition: all .1s ease;
  font-weight: 300;
}
.start-game__btn:hover{
  background-color: #fff;
  color: #462446;
  font-weight: 500;
}

.field__info{
  margin-top: 25px;
}

.score{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 25px;
  width: 300px;
  border: 2px solid #fff;
  padding: 10px;
}
.score_descr{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 10px;
}
.score_item{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  
}
.score_item div{
  width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.score_descr div{
  width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.field{
  width: 300px;
  height: 300px;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;  
}
.cell{
  width: 100px;
  height: 100px;
  border: 1px solid #462446;
  outline: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #fff;
  color: #462446;
  font-size: 60px;
  font-weight: 900;
  transition: all .2s ease;
}

.cell:hover{
  cursor: pointer;
  background-color: rgb(235, 235, 235);  
}
.main-menu{
  position: fixed;  
  -webkit-transform: translateX(-105%);  
          transform: translateX(-105%);
}

.main-menu.active{
  background-color: #B05F6D;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;  
  -webkit-transform: translateX(0);  
          transform: translateX(0);
  color: #fff;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}

.main-menu__content{
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: all .3s ease;
  font-size: 21px;
}

.title{
  margin-bottom: 50px;
}

.content-item{
  width: 350px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 25px;
}
.content-item label{
  margin-right: 10px;
}
.content-item input{
  width: 190px;
  padding: 5px 10px;
  outline: none;
  border: none;
  font-weight: 700;
}

.main-menu.active > .menu > .close-menu__btn{
  position: fixed;
  font-weight: 900;
  font-size: 30px;
  cursor: pointer;
  margin-top: 20px;
  margin-left: 20px;
}

.chekbox-content__item input{
  width: 30px;
  height: 30px;
  background: none;
  border: none;
  justify-self: flex-end;
}

.apply-changes{
  margin-bottom: 50px;
  font-size: 17px;
  padding: 10px 15px;
  font-weight: 900;
  color: #B05F6D;
  background-color: #fff;
}
